@import "../../../variables.scss";

.root {
  width: 100%;
  padding: 0.5rem;
  font-size: 1rem;

  &.primary {
    border: 1px solid $color-text-default;
    color: $color-text-default;
    background-color: transparent;
  }

  &.secondary {
    border: none;
    border-radius: 0.3rem;
    font-weight: bold;
    color: $color-text-light;
    background-color: $color-primary-dark;
  }

  &.third {
    padding: 1rem;
    border: none;
    background-color: $color-text-default;
    color: $color-text-light;
    box-shadow: inset 0 0 20px 8px $color-primary;
  }

  &.disabled {
    border-color: gray;
    color: gray;
    cursor: default;
  }

  &:not(.disabled) {
    &:hover {
      background-color: rgba(125, 102, 72, 0.2);
    }
  }
}