@import "../../../variables.scss";

.root {
  $size: 54px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  width: $size;
  height: $size;
  border-radius: 50%;
  background-color: $color-primary-dark;
  cursor: pointer;

  img {
    width: 100%;
    height: 100%;
  }
}