.root {
  height: 100%;
  border: none;
  background: none;

  &:hover {
    .imageContainer {
      background-image: var(--src-hover);
    }
  }

  &:disabled {
    .imageContainer {
      background-image: var(--src-disabled);
    }
  }

  .imageContainer {
    height: 100%;
    background-image: var(--src-default);
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }
}