@import "../../../variables.scss";

.root {
  height: 100%;
  overflow: hidden;
  display: grid;
  grid-template-rows: auto 1fr auto;
  justify-content: space-between;
  grid-gap: 1rem;
  padding: 0 1rem;
}

.top {
  margin: 1rem 0;

  .characters {
    display: flex;
    justify-content: center;

    .characterItem {
      margin-left: 1rem;

      &:first-of-type {
        margin-left: 0;
      }
    }
  }

  .characterName {
    margin-top: 1rem;
    font-size: 1.2rem;
    text-align: center;
    color: $color-text-light;
  }
}

.middle {
  .card {
    display: grid;
    align-items: center;
    grid-template-columns: 2fr 4fr 2fr;
    grid-gap: 1rem;

    .left {
      display: grid;
    }

    .right {
      display: grid;
      grid-gap: 1rem;
      justify-items: center;
      align-items: center;
    }
  }

  .cardContainer {
    margin: 0 auto;
    cursor: pointer;

    img {
      max-height: 256px;
    }
  }

  .actions {
    margin-top: 2rem;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
  }
}

.cardFullScreenContainer {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  padding: 1rem;
  background: $color-primary;
  text-align: center;

  .imageContainer {
    height: 60vh;
    cursor: pointer;

    img {
      height: 100%;
    }
  }
}

.healthPoints {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3.5rem;
  height: 3.5rem;
  border: 2px solid $color-border-dark;
  border-radius: 50%;
  font-size: 1.2rem;
  background: #7C0301;
  color: $color-text-light;
  box-shadow: 0 0 4px 0 $color-shadow-dark;
}

.bottom {
  margin: 0 -1rem;
  text-align: center;
}