@import "../../../variables.scss";

.root {
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: 0 1rem;
}

.top {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(3, 1fr);
  margin: 1.5rem 0;
  text-align: center;

  .buttonChangeRound {
    width: 93px;
    justify-self: center;
  }

  .round {
    margin: 0 1rem;
    color: $color-text-light;

    span {
      vertical-align: middle;
    }
  }
}

.activationTokens {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(4, 1fr);
  margin: 1.5rem 0;
  justify-items: center;
}

.statistics {
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-gap: 1rem;
  margin: 1rem 0;

  .gloryPointsControls {
    display: grid;
    grid-gap: 1rem;
  }

  .gloryPoints {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;

    .gloryPoint {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 1rem;
      background: #102626;
    }
  }
}

.bottom {
  display: grid;
  height: 100%;
  padding-top: 1rem;
  text-align: center;
  grid-template-rows: 1fr auto;
  margin: 0 -1rem;

  .buttonEndGame {
    align-self: flex-start;
  }
}