@import "../../../variables.scss";

.root {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0 2rem;
  align-content: center;
  justify-items: center;
  height: 3rem;
  background-color: #132C2C;
  padding: 0.25rem 1.5rem;

  a {
    &.active {
      button {
        color: $color-text-default;
      }
    }
  }
}