@import "../../../variables.scss";

.outer {
  overflow: hidden;
  box-sizing: initial;
}
.inner {
  box-sizing: initial;
  border: 1px solid $color-text-default;
}
.inner i {
  width: 1rem;
  height: 1rem;
  border: 1px solid $color-text-default;
  border-radius: 50%;
  background: $color-primary;
}
.inner .top {
  margin-top: -0.5rem;
}
.inner .bottom {
  margin-top: -0.5rem;
  margin-bottom: -0.5rem;
}
.inner .left {
  float: left;
  margin-left: -0.5rem;
}
.inner .right {
  float: right;
  margin-right: -0.5rem;
}
.content {
  min-height: 2rem;
}