@import "../../../variables.scss";

.root {
  text-align: center;
  cursor: pointer;

  &.focused {
    .imageContainer {
      position: relative;

      &:after {
        z-index: 1;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        border: 2px solid #92632A;
        border-radius: 50%;
        content: "";
      }
    }
  }
}

.imageContainer {
  $size: 54px;
  overflow: hidden;
  width: $size;
  height: $size;
  border-radius: 50%;
  margin: 0 auto;
  background-color: $color-gray;
  box-shadow: inset 0 0 5px 4px black;

  img {
    width: 100%;
    height: 100%;
  }
}